
import {Injectable} from '@angular/core';
import {DockedPanel, DockedPanelDirection, DockedPanelFooterAlign} from './docked-panel.model';

/**
 * DockedPanel configuration service
 */
@Injectable({
  providedIn: 'root'
})
export class AdaptDockedPanelConfig implements DockedPanel {
  direction: DockedPanelDirection = DockedPanelDirection.DEFAULT;
  title: string = '';
  content: string = '';
  floatFooter: boolean = false;
  footerAlign: DockedPanelFooterAlign = DockedPanelFooterAlign.DEFAULT;
  hideBackdrop: boolean = false;
  blockDismiss: boolean = true;
  blockKeyboard: boolean = false;
  size: string = 'default';
}
